//Animate CSS + WayPoints javaScript
//Example: $(".element").animated("zoomInUp");
(function($) {
	$.fn.animated = function(inEffect) {
		$(this).each(function() {
			var animateElement = $(this);
			animateElement.css('opacity', '0').addClass('animated').waypoint(function(position) {
				if (position === 'down') {
					animateElement.addClass(inEffect).css('opacity', '1');
				};
			}, {
				offset: '90%'
			});

		});
	};
})(jQuery);
$(document).ready(function() {

    // Waypoints Animations
    $(window).load(function() {
        $('.scrollimation').waypoint(function(){
            $(this).addClass('in');
        },{offset:'95%'});
    });

    // Refresh scrollSpy function
    function scrollSpyRefresh(){
        setTimeout(function(){
            $('body').scrollspy('refresh');
        },1000);
    }

    // Refresh waypoints function
    function waypointsRefresh(){
        setTimeout(function(){
            $.waypoints('refresh');
        },1000);
    }

    $('.skills').waypoint(function() {
        $('.chart').each(function() {
        $(this).easyPieChart({
                size: 130,
                animate: 2000,
                lineCap:'butt',
                scaleColor: false,
                trackColor: 'transparent',
                barColor: '#00CCCB',
                lineWidth: 5,
                easing:'easeOutQuad'
            });
        });
    }, {offset:'80%'});

    $("[data-toggle='tooltip']").tooltip({container: 'body'});

});
$(document).ready(function(){

    $('#contact-form .form-control').each(function(){

        if ($.trim($(this).val()) === ''){
            $(this).removeClass('input-filled');
        } else {
            $(this).addClass('input-filled');
        }
    });

    $('#contact-form .form-control').on('blur',function(){

        if ($.trim($(this).val()) === '') {
            $(this).removeClass('input-filled');
        } else {
            $(this).addClass('input-filled');
        }
    });

});
$(document).ready(function() {

    // Sandwich toggle
    $('.sandwich').click(function() {
        $('.sandwich').toggleClass('active');
    });

    // Navigation Functions
    if ($(window).scrollTop()< 10) {
        $('#main-nav').removeClass('scrolled');
    }
    else{
        $('#main-nav').addClass('scrolled');
    }

    $(window).scroll(function() {
        if ($(window).scrollTop()< 10) {
            $('#navigation').removeClass('scrolled');
        }
        else{
            $('#navigation').addClass('scrolled');
        }
    });

    $('a.scrollto').click(function(e) {
        e.preventDefault();
        var target =$(this).attr('href');
        $('html, body').stop().animate({scrollTop: $(target).offset().top}, 1600, 'easeInOutExpo',
            function(){window.location.hash = target;});

        if ($('.navbar-collapse').hasClass('in')){
            $('.navbar-collapse').removeClass('in').addClass('collapse');
        }
    });


});
$(window).on('load', function () {
    var $preloader = $('#page-preloader'),
        $spinner   = $preloader.find('.fa');

    $spinner.fadeOut();
    $preloader.delay(350).fadeOut('slow');
});
